var processAudio = function (item) {
    var audio = item.closest('.item').querySelector('.item__audio');
    if (item.closest('.item').classList.contains('active')) {
        audio.play();
    }
    else {
        audio.pause();
    }
};
var disactiveOthers = function (currentItem) {
    document.querySelectorAll('.item__view').forEach(function (item) {
        if (currentItem === item)
            return;
        item.closest('.item').classList.remove('active');
        document.querySelector("[data-bg=\"".concat(item.dataset.no, "\"]")).classList.remove('active');
        processAudio(item);
    });
};
var itemClick = function (item) {
    item.closest('.item').classList.toggle('active');
    document.querySelector("[data-bg=\"".concat(item.dataset.no, "\"]")).classList.toggle('active');
    processAudio(item);
    disactiveOthers(item);
};
var initVolume = function () {
    document.querySelectorAll('.item').forEach(function (item) {
        var audio = item.querySelector('.item__audio');
        var range = item.querySelector('.item__range');
        range.value = '50';
        audio.volume = +range.value / 100;
        range.addEventListener('input', function () {
            audio.volume = +range.value / 100;
        });
    });
};
export var initialize = function () {
    initVolume();
    document.querySelector('.items').addEventListener('click', function (e) {
        var itemView = e.target.closest('.item__view');
        if (itemView)
            itemClick(itemView);
    });
};
